/*
 * [Modified] Modern CSS Reset
 * @link https://github.com/hankchizljaw/modern-css-reset
*/
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

html,
body {
  overflow-x: hidden;
}

html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  font-family: sans-serif;
  font-size: 100%;
  line-height: 1.5;
  text-rendering: optimizeSpeed;
}

/* Make images easier to work with */
img {
  display: block;
  max-width: 100%;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
  html {
    scroll-behavior: initial;
  }
}
body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  width: 100%;
  width: 100%;
  position: relative;
  overflow-y: scroll;
}
body.mobile-menu-expanded {
  overflow: hidden;
}

/* stick footer on the bottom */
#page {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
  flex-grow: 1;
}

footer.site-footer {
  display: flex;
  background-color: #2DD3EB;
}

/* page layouts */
#content #main > * {
  padding-left: clamp(25px, 10vw, 146px);
  padding-right: clamp(25px, 10vw, 146px);
}
#content #main .alignwide {
  padding-left: 0;
  padding-right: 0;
}

/* index layouts */
.postwrap {
  padding-left: clamp(25px, 10vw, 146px);
  padding-right: clamp(25px, 10vw, 146px);
  padding-top: 12.5px;
  padding-bottom: 12.5px;
}
.postwrap:first-of-type {
  padding-top: 0;
}
.postwrap:last-of-type {
  padding-bottom: 50px;
}
.postwrap.alignwide {
  padding-left: 0;
  padding-right: 0;
}
.postwrap .layout {
  width: 100%;
  position: relative;
  display: flex;
  gap: clamp(18.75px, 2vw, 25px);
}
.postwrap .layout.reverse {
  flex-direction: row-reverse;
}
.postwrap .layout .left {
  display: flex;
  flex-direction: column;
  width: 35%;
}
.postwrap .layout .right {
  display: flex;
  flex-direction: column;
  width: 65%;
}

@media screen and (max-width: 991px) {
  .postwrap .layout {
    flex-wrap: wrap;
  }
  .postwrap .layout .left {
    width: 100%;
  }
  .postwrap .layout .right {
    width: 100%;
  }
}
/* variables */
/* classes */
.fadein {
  animation: fadeIn 0.444s ease;
}

.fadeout {
  animation: fadeOut 0.444s ease;
}

.slidein {
  animation: slideIn 0.444s ease;
}

.slideout {
  animation: slideOut 0.444s ease;
}

/* Animations */
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes fadeOut {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes slideIn {
  0% {
    left: -101vw;
  }
  100% {
    left: 0;
  }
}
@-webkit-keyframes slideIn {
  0% {
    left: -101vw;
  }
  100% {
    left: 0;
  }
}
@keyframes slideOut {
  0% {
    left: 0;
  }
  100% {
    left: -101vw;
  }
}
@-webkit-keyframes slideOut {
  0% {
    left: 0;
  }
  100% {
    left: -101vw;
  }
}
/* SPINNER */
.logo-spinner {
  position: absolute;
  top: calc(50% - 87px);
  left: calc(50% - 87px);
  background-image: url(/theme/assets/icons/spinner-logo.svg);
  background-size: 50%;
  background-repeat: no-repeat;
  background-position: center center;
  z-index: 1;
}

.spinner {
  animation: rotate 1.4s linear infinite;
  -webkit-animation: rotate 1.4s linear infinite;
  -moz-animation: rotate 1.4s linear infinite;
  width: 174px;
  height: 174px;
  position: relative;
}

.spinner {
  animation: rotate 1.4s linear infinite;
  -webkit-animation: rotate 1.4s linear infinite;
  -moz-animation: rotate 1.4s linear infinite;
  width: 174px;
  height: 174px;
  position: relative;
}

.spinner-dot {
  width: 274px;
  height: 274px;
  position: relative;
  top: 0;
}

@keyframes rotate {
  to {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes rotate {
  to {
    -webkit-transform: rotate(360deg);
  }
}
@-moz-keyframes rotate {
  to {
    transform: rotate(360deg);
  }
}
.path {
  stroke-dasharray: 170;
  stroke-dashoffset: 20;
}

