.wp-block-button__link,
button.primary,
.button.primary,
.button, button {
  background-color: #2DD3EB;
  color: #fff;
  cursor: pointer;
  padding-right: clamp(23.94px, 3.33vw, 47.88px);
  padding-left: clamp(23.94px, 3.33vw, 47.88px);
  padding-top: clamp(8.24px, 1.14vw, 16.47px);
  padding-bottom: clamp(8.24px, 1.14vw, 16.47px);
  font-size: clamp(16px, 1.25vw, 18px);
  line-height: clamp(24px, 2.22vw, 32px);
  width: fit-content;
  border-radius: 4px;
  text-align: center;
  white-space: nowrap;
  transition: all 0.33s ease;
}
.wp-block-button__link:hover,
button.primary:hover,
.button.primary:hover,
.button:hover, button:hover {
  /* Darken on click by 15% (down to 85%) */
  filter: brightness(0.85);
}
