button.primary:focus,
button.primary.focus,
.button.primary:focus,
.button.primary.focus,
.button:focus,
.button.focus, button:focus, button.focus {
  filter: brightness(1.1);
  border: 1px solid #2DD3EB;
}
.wp-block-button__link:disabled, .wp-block-button__link.disabled,
button.primary:disabled,
button.primary.disabled,
.button.primary:disabled,
.button.primary.disabled,
.button:disabled,
.button.disabled, button:disabled, button.disabled {
  background-color: #D9D9D9;
  color: #fff;
}
.wp-block-button__link.outline,
button.primary.outline,
.button.primary.outline,
.button.outline, button.outline {
  background-color: transparent;
  outline: 1px solid #2DD3EB;
  color: #2DD3EB;
}
.wp-block-button__link.outline:hover,
button.primary.outline:hover,
.button.primary.outline:hover,
.button.outline:hover, button.outline:hover {
  background-color: #2DD3EB;
